// styles go here
html {
  font-family: "Roboto", sans-serif;
}

body {
  margin: 0;
  background-color: #d7d08a;

}

.list {
  &__item {
    padding: 5px;
    margin-bottom: 10px;
    border-radius: 15px;
    width: max-content;
    &--selected {
      background-color: yellowgreen;
    }
  }
}

.btn {
  border: 2px solid #1a1a1a;
  border-radius: 15px;
  color: #3b3b3b;
  cursor: pointer;
  display: inline-block;
  margin: 0 10px;
  min-height: 30px;
  padding: 8px 16px;
  text-align: center;
  transition: all 0.5s cubic-bezier(0.23, 1, 0.32, 1);

  &:hover {
    color: #fff;
    background-color: #1a1a1a;
    box-shadow: rgba(0, 0, 0, 0.25) 0 8px 15px;
  }
  
}
